import React, {useCallback, useEffect, useRef, useState} from 'react';
import axios from 'axios';
import {useLocation, useNavigate} from 'react-router-dom';
import '../../../css/boardDetail.css';

export default function View() {
    const [board, setBoard] = useState([]);                     //    
    let freeBoardNo = useLocation().state;                                            //  page   freeBoardNo
    const boardType = "noticeBoard";                                           // URL   

    const navigate = useNavigate();
    const [memberLog, setMemberLog] = useState("");            //   

    const [commentList, setCommentList] = useState([]);         //     
    const [commentContent, setCommentContent] = useState("");  //  
    const typeRef = useRef("");              //     TYPE   useRef
    const contentRef = useRef("");           //  <input>   Ref
    const [updateBtn, setUpdateBtn] = useState(false);        //      button
    const [tempCommentNo, setTempCommentNo] = useState(0);     //      commentNo

    //  handler
    const contentOnChangeHandler = useCallback((e) => {
        setCommentContent(e.target.value)
    }, [])

    //  
    const deletePost = async () => {
        if(window.confirm(" ?")){
            const res = await axios.get(`/${boardType}/delete?${boardType}No=${freeBoardNo}`)
                .then((navigate(`/community/${boardType}/list`)))
                .catch((error)=> console.log("  "))
        }
    }

    //  (update.js  )
    const updatePost = () => {
        navigate(`/community/${boardType}/update`,{state:{freeBoardNo}})
    }

    //    
    const getBoard = async () => {
        // TODO
        // ''     1 page     currentPage  
        const res = await axios.get(`/${boardType}/view?${boardType}No=${freeBoardNo}`)
        if (res.data) {
            setBoard(res.data);
        }
    };

    //   
    const getCommentList = async () => {
        const res = await axios.get('/comment/list',{
            params:{
                boardNo : freeBoardNo,
                type : typeRef.current.value,
            }
        })
        if (res.status === 200) {
            setCommentList(res.data);
        }
    };

    //  
    const goRegisterComment = async () => {
        if(contentRef.current.value){
            const data = {
                boardNo:freeBoardNo,
                memberId:memberLog.memberId,
                content : commentContent,
                type : typeRef.current.value,
            }
            const res = await axios.post("/comment/register",data)
            if(res.status === 200) {
                contentRef.current.value = "";
                getCommentList(); //     commentList  
            }
        }else {
            alert(" ")
            contentRef.current.focus();
        }
    }

    //  
    const updateComment = async (e) => {
        contentRef.current.value = e.content;
        contentRef.current.focus();
        setTempCommentNo(e.commentNo);
        setUpdateBtn(true);  //    ''   ''  
    }

    //    DB 
    const goUpdateComment = async () => {
        const data = {
            commentNo : tempCommentNo,
            content : commentContent,
            type : typeRef.current.value,
        }
        const res = await axios.post("/comment/update", data)
        contentRef.current.value = "";
        setUpdateBtn(false); //    ''   ''  
        // getCommentList();
    }

    //  
    const deleteComment = async (e) => {
        const commentNo = e.commentNo; //   
        const res = await axios.post("/comment/delete", null, {
            params: {
                commentNo: commentNo,
            }
        });
        if (res.status === 200) {
            // getCommentList();
        }
    }

    //    
    const goAnnouncement = async (e) => {
        if(window.confirm("  ?")){
            const res = await axios.get(`/${boardType}/announcement?${boardType}No=${e}`)
                .then(navigate(`/community/${boardType}/list`))
                .catch((error => console.log("error occurred!")))
        }
    }

    // TODO
    // ,     MEMBER_ID   MEMBER_NICKNAME  
    // axios.get   MEMBER_ID  MEMBER   

    useEffect(() => {
        getBoard();
        // getCommentList();
        const getMemberLog = sessionStorage.getItem("member");
        if(getMemberLog){
            const parse = JSON.parse(getMemberLog);
            setMemberLog(parse);
        }
    }, []);

    return (
        <>
            {board ? (
                <div className="container" style={{marginTop: "10rem", marginLeft: "6rem"}}>
                    <div className="header">
                        <div className="text-black text-2xl my-3">{board.noticeTitle}</div>
                        <div>
                            <span>  : {board.noticeBoardNo}&nbsp;&nbsp;&nbsp;</span>
                            <span> : {board.memberId}</span>
                        </div>
                        <div> : {new Date(board.createDate).toLocaleString('ko-kr', {
                            year: "numeric",
                            month: "long",
                            day: "numeric",
                            hour: "numeric",
                            minute: "numeric",
                            second: "numeric"
                        })}
                            <span className="float-right mr-4">: {board.hits}</span>
                        </div>
                    </div>
                    <div className="comment" dangerouslySetInnerHTML={{__html: board.noticeContent}}/>
                    <div className="actions me-4">
                        {
                            memberLog && memberLog.memberAdmin === 'ADMIN'
                                ?
                                <>
                                    <button className="bg-amber-500 text-white font-bold"
                                            onClick={() => goAnnouncement(freeBoardNo)}> 
                                    </button>
                                    <button className="bg-red-500 text-white font-bold" onClick={deletePost}></button>
                                </>
                                :
                                <>
                                    {
                                        memberLog.memberId === board.memberId
                                            ?
                                            <>
                                                <button className="bg-blue-500 text-white font-bold"
                                                        onClick={updatePost}>
                                                </button>
                                                <button className="bg-red-500 text-white font-bold"
                                                        onClick={deletePost}>
                                                </button>
                                            </>
                                            :
                                            null
                        }
                                </>
                        }
                        <button onClick={() => navigate(`/community/${boardType}/list`)}
                                className="bg-gray-500 text-white font-bold">
                        </button>
                        <button onClick={() => navigate(-1)}
                                className="bg-black text-white font-bold">
                        </button>
                    </div>
                    <hr className="my-10"/>
                </div>
            ) : (
                <p>Loading...</p>
            )}
        </>
    );
};
