import {useEffect, useState} from "react";
import {Link, useParams, useSearchParams} from "react-router-dom";
import axios from "axios";
import "../../css/board.css"

export default function BookView() {


    /**
     *  : 
     *    
     * getBook() :        
     * */


    /*   */
    const [book, setBook] = useState({});

    /*     */
    const [bookRent, setBookRent] = useState(true);
    const memberObj = JSON.parse(sessionStorage.getItem("member"));

    /* url     */
    const [searchParams, setSearchParams] = useSearchParams();

    /* ?bookNo=n  n  bookNo  */
    const bookNo = searchParams.get("bookNo");

    /*       */
    const getBook = async () => {
        try {
            const res = await axios.get("/book/view", {
                params: {
                    bookNo: bookNo
                }
            });
            setBook(res.data);
            if (res.data.bookRent == 'N') {
                //  
                setBookRent(true)
            } else if (res.data.bookRent == 'Y') {
                setBookRent(false);
            }

        } catch (error) {
            console.error("Error fetching data:", error);
        }
    };

    /**
     *  : 
     *     
     * checkRentCount() :         
     * bookRentEvent
     * */

        //     
    const bookRentEvent = async () => {
            if (!bookRent) {
                alert("    .");
                return;
            } else {
                //      
                //    
                const res = await axios.post("/book/checkOverdueDays", null, {
                    params: {
                        memberNo: memberObj.memberNo
                    }
                }, {
                    headers: {
                        "Content-Type": "application/json"
                    }
                });
                sessionStorage.setItem("bookRent", JSON.stringify("ok")) //    
                console.log(": ", res.data)

                //     /
                if (!bookRent) {
                    alert("    .");
                    return;
                } else {
                    //      
                    //    

                    //    ( )
                    const res = await axios.post("/book/checkOverdueDays", null, {
                        params: {
                            memberNo: memberObj.memberNo
                        }
                    }, {
                        headers: {
                            "Content-Type": "application/json"
                        }
                    });

                    //  0   
                    if (res.data > 0) {
                        return alert(`  ${res.data} .    .`)


                        //      (res.data 3   )
                    } else if (res.data < 0 || !res.data) {
                        const response = await axios.post("/book/checkRentCount", null, {
                            params: {
                                memberNo: memberObj.memberNo
                            }
                        }, {
                            headers: {
                                "Content-Type": "application/json"
                            }
                        })

                        if (response.data >= 3) {
                            return alert("    3.")

                        }
                        //       
                        const res = await axios.post("/book/rent", null, {
                            params: {
                                bookNo: bookNo,
                                bookName: book.bookName,
                                memberNo: memberObj.memberNo,
                                memberId: memberObj.memberId
                            }
                        }, {
                            headers: {
                                "Content-Type": "application/json"
                            }
                        });

                        //              
                        //   
                        if (res.status == 200) {
                            //    
                            updateBookStatus();
                            alert("  ");
                            window.location.href = '/mypage';
                            //    
                        } else {
                            alert("  ,  .");
                        }
                    }
                }
            }
    }
    /*     ->     */
    const updateBookStatus = async () => {
        const res = await axios.post("/book/status", null, {
            params: {
                bookNo: bookNo
            }
        })
        if (res.status == 200) {
            console.log(res)
        }
    }


    /*    window     */
    useEffect(() => {
        window.scrollTo({
            top: 0,
            behavior: "auto"
        });
        getBook();
    }, []);



    return (
        <main>
            <section className="gj do hj sp jr i pg">
                <div>

                    <div className="page-title">
                        <div className="container">
                            <img
                                src="https://i0.wp.com/library.re.kr/wp-content/uploads/2022/08/social-1024x512-1.jpeg"
                                alt="Example"
                                style={{
                                    width: '100%',
                                    height: '300px',
                                    display: "flex",
                                    justifyContent: "center",
                                    alignItems: "center"
                                }}
                            />
                        </div>
                    </div>
                    <div id="board-list" className="rundry">
                        <div className="container">
                            <div style={{
                                display: "flex",
                                justifyContent: "center",
                                alignItems: "center",
                                textAlign: "center"
                            }}>
                                  <br/>      . <br/>     .<br/> !
                            </div>
                            <div style={{marginTop: "4%", marginBottom: "2%"}}>
                                {/*<img src="https://w7.pngwing.com/pngs/676/110/png-transparent-barcode-book-education-library-loan-school-library-icon.png" />*/}


                                {memberObj ? <button
                                    className="bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded my-2"
                                    onClick={bookRentEvent}>
                                     
                                </button> : null}


                                <Link to={`/book/list`}>
                                    <button
                                        className="bg-transparent hover:bg-blue-500 text-blue-700 font-semibold hover:text-white py-2 px-4 border border-blue-500 hover:border-transparent rounded mx-2">
                                        
                                    </button>
                                </Link>
                            </div>
                            <table className="board-table" style={{fontSize: "15px"}}>
                                <thead>
                                <th style={{background: "lightgray"}} scope="col" className="th-num"></th>
                                <td style={{display: "flex", justifyContent: "center", alignItems: "center"}}>

                                    <img
                                        src={book.bookImage}
                                        alt=" "
                                        className="flex justify-center items-center"
                                    />
                                </td>
                                </thead>
                                <tbody>
                                <tr>
                                    <th style={{background: "lightgray"}} scope="col" className="bookDetailTitle">
                                    </th>
                                    <td scope="col">{book.bookNo}</td>
                                </tr>
                                <tr>
                                    <th style={{background: "lightgray"}} scope="col" className="bookDetailTitle">
                                    </th>
                                    <td>{book.bookName}</td>
                                </tr>
                                <tr>
                                    <th style={{background: "lightgray"}} scope="col" className="bookDetailTitle">
                                    </th>
                                    <td>{book.bookAuthor}</td>
                                </tr>
                                <tr>
                                    <th style={{background: "lightgray"}} scope="col" className="bookDetailTitle">
                                    </th>
                                    <td>{book.bookCompany}</td>
                                </tr>
                                <tr>
                                    <th style={{background: "lightgray"}} scope="col"
                                        className="bookDetailTitle white-space: nowrap">
                                    </th>
                                    <td>{book.bookIntro}</td>
                                </tr>
                                <tr>
                                    <th style={{background: "lightgray"}} scope="col" className="bookDetailTitle">
                                    </th>
                                    <td>{book.bookRelease}</td>
                                </tr>
                                <tr>
                                    <th style={{background: "lightgray"}} scope="col"
                                        className="bookDetailTitle"><br/>
                                    </th>
                                    <td>
                                        {bookRent ? <img
                                                src="/images/book_possible.png"
                                                alt=" "
                                                className="flex justify-center items-center"
                                                style={{width: "50px", height: "50px", marginLeft: "48%"}}/>
                                            : <img
                                                src="/images/book_impossible.png"
                                                alt=" "
                                                className="flex justify-center items-center"
                                                style={{width: "50px", height: "50px", marginLeft: "48%"}}/>
                                        }
                                    </td>
                                </tr>
                                </tbody>
                            </table>

                        </div>
                    </div>
                </div>
            </section>
        </main>
    )

}
