import React, {useCallback, useEffect, useRef, useState} from 'react';
import axios from 'axios';
import '../../../css/boardList.css'
import {Link, useNavigate} from "react-router-dom";

export default function List () {
    const [relayBoardNo, setRelayBoardNo] = useState(0); //    
    const [boardList, setBoardList] = useState([]);
    const [totalBoardList, setTotalBoardList] = useState([]);

    const [memberId, setMemberId] = useState("");
    const [addBtn, setAddBtn] = useState(false);                    //    
    const [commentBtn, setCommentBtn] = useState(false);            //   
    const typeRef = useRef();                                 // DB     Ref

    const [title, setTitle] = useState("");         //   (RELAY_BOARD)
    const [content, setContent] = useState("");     //  (COMMENT)
    const navigate = useNavigate();

    // handler
    const titleOnChangeHandler = useCallback((e) => {
        setTitle(e.target.value);
    }, [])

    const contentOnChangeHandler = useCallback((e) => {
        setContent(e.target.value);
    }, [])

    // TODO
    //     |  | 06.28()
    //             

    // DB    
    const getData = async () => {
        const res = await axios.get(`/relayBoard/list`)
        if (res.data) {
            setBoardList(res.data.boardList);
        }
    }

    // AMDIN   
    const goDelete = async (number)=> {
        if(window.confirm("  : " + number + "  ?")){
            const res = await axios.put(`/relayBoard/delete`, null, {
                params:{
                    relayBoardNo: number,
                }
            })
            getData(); //    
        }
    }

    //      
    const goAdd = () => {
        if (!memberId) {
            alert("  .");
            navigate("/signIn")
        }
        setTitle("");
        setAddBtn(!addBtn);
    }

    //  
    const goRegister = async ()=> {
        const res = await axios.post("/relayBoard/register", null,{
            params : {
                relayTitle : title,
                memberId : memberId.memberId,
            }
        })
        setRelayBoardNo(res.data);
        setAddBtn(false);
        getData();
    }

    //   
    const goCancle = () => {
        setAddBtn(false);
    }

    //    
    const [commentIndex, setCommentIndex] = useState(0);
    const goChangeComment = (e) => {
        if (!memberId) {
            alert("  .");
            navigate("/signIn");
            return;
        }
        setContent("");
        setCommentIndex(e);
        setCommentBtn(!commentBtn);
    }

    // GPT   
    const goGPT = (e) => {
        console.log("GPT ",e);
        console.log("  : ",e.relayTitle);
        console.log("  : ",e.commentList);
    }

    //  
    const goCommentRegister = async () => {
        const data = {
            boardNo: relayBoardNo,
            content : content,
            memberId : memberId.memberId,
            type : typeRef.current.value,
        }
        const res = await axios.post("/comment/register", data)
        if(res.status){
            setCommentBtn(false);
            getData();
        }
    }

    //   
    const goCommentCancle = async () => {
        setCommentBtn(false);
    }

    //  
    const goDeleteComment = async (e) => {
        const res = await axios.post(`/comment/delete?commentNo=${e}`)
        getData();
    }
    useEffect(() => {
        console.log("  : ",memberId);
    }, [memberId]);

    useEffect(() => {
        console.log("  : ", boardList)
    }, [boardList]);

    //     
    useEffect(() => {
        getData()
        const memberLog = sessionStorage.getItem("member")
        if(memberLog){
            setMemberId(JSON.parse(memberLog)); //   memberId, memberNickname, memberAdmin  data 
        }
    }, []);

    return (
        <article className="mt-32 ml-32 mr-32">
            <br/>
            <div className="text-center text-4xl mb-10"> </div>
            <hr className="hr1" noshade/>
            <span> {boardList.length}   .</span>
            <button onClick={goAdd} className="bg-blue-600 text-white font-bold p-1 rounded">  </button>
            <div className="my-5">
                {addBtn
                ?
                    <div className="border rounded bg-blue-100">
                    {memberId
                        ? <>
                        <span className="ms-5">
                            <div className="grid grid-cols-1 ms-10">
                                <img className="ms-5 mt-5 w-10"
                                     src={`http://localhost:3000${memberId.memberProfile}`}
                                     alt={""}/>
                                <span className="top-5 font-bold text-black">{memberId.memberId}</span>
                            </div>
                        </span>
                            <input type="text"
                                   maxLength={50}
                                   placeholder={"50  "}
                                   className="w-full h-10 bg-blue-200"
                                   onChange={titleOnChangeHandler}/>
                            <div className="grid grid-cols-2">
                                <div className="ms-10">
                                    {title.length >= 40
                                        ?
                                        <div className="text-red-600">  {title.length}</div>
                                        :
                                        <div>  {title.length}</div>
                                    }
                                </div>
                                <div className="text-end">
                                <button className="me-5 mb-3 font-bold" onClick={goRegister}></button>
                                <button className="me-5 mb-3 font-bold" onClick={goCancle}></button>
                                </div>
                            </div>
                        </>
                        :
                        null
                    }
                    </div>
                :
                null
                }
            </div>
            <hr className="my-5"/>
            {boardList && boardList.map((v, i) => {
                return(
                    <div key={i} className="my-5">
                        <div className="border rounded">
                            <div>  : {v.relayBoardNo}</div>
                            <div>  : {v.relayTitle}</div>
                            <div> : {v.memberId}</div>
                            <div>  :
                                <img className="ms-5 mt-5 w-12 inline rounded-full"
                                     src={`http://localhost:3000${v.memberProfile}`}/>
                            </div>
                            <div> : {new Date(v.modifyDate).toLocaleString('ko-kr', {
                                year: "numeric",
                                month: "long",
                                day: "numeric",
                                hour: "numeric",
                                minute: "numeric",
                                second: "numeric"
                            })}
                            </div>
                            <div> : {v.status}</div>
                            <button onClick={() => {
                                goChangeComment();
                                setRelayBoardNo(v.relayBoardNo);
                                setCommentIndex(i);
                            }} className="bg-blue-600 text-white font-bold p-2 rounded"> 
                            </button>
                            {commentBtn && commentIndex === i
                                ?
                                <div className="border rounded p-10">
                                    <span className="text-black font-bold">{memberId.memberId}</span>
                                    <input type="text"
                                           maxLength={50}
                                           onChange={contentOnChangeHandler}
                                           placeholder="50  "
                                           className="bg-blue-100 w-full h-12 rounded"/>
                                    <div className="grid grid-cols-2">
                                        <div className="text-center">
                                            {content.length >= 40
                                                ?
                                                <span className="text-red-600">  {content.length}</span>
                                                :
                                                <span className="text-start">   {content.length}</span>
                                            }
                                            <button className="me-5 mb-3 font-bold" onClick={()=>goGPT(v)}>
                                                <img src="/images/GPT_logo.png" className="w-8 mt-3"/>
                                            </button>
                                        </div>
                                        <div className="mt-5 text-end">
                                            <button className="me-5 mb-3 font-bold" onClick={goCommentRegister}>
                                            </button>
                                            <button className="me-5 mb-3 font-bold" onClick={goCommentCancle}>
                                            </button>
                                        </div>
                                    </div>
                                </div>
                                :
                                null
                            }
                        </div>
                        <div className="my-5 mb-14">
                            <div>{v.commentList.map((c, j) => {
                                return (
                                    <div key={j} className="my-12">
                                            <>
                                                <img src="/images/comment_arrow.png" className="w-16 ms-20 inline"/>
                                                {
                                                    c.status === 'Y'

                                                    ?
                                                        <>
                                                        <div className="inline border ms-10 p-10 text-black">
                                                    <span><img src={`http://localhost:3000${c.memberProfile}`}
                                                               className="rounded-full w-12 inline"/></span>
                                                            <span>  : {c.commentNo}</span>
                                                            <span> : {new Date(c.modifyDate).toLocaleString('ko-kr', {
                                                                year: "numeric",
                                                                month: "long",
                                                                day: "numeric",
                                                                hour: "numeric",
                                                                minute: "numeric",
                                                                second: "numeric"
                                                            })}
                                                            </span>
                                                                <span className="text-black">{c.memberId}</span>
                                                            <span className="text-black"> : {c.content}</span>
                                                        </div>
                                                        {c.memberId === memberId.memberId
                                                                ?
                                                                <button onClick={() => goDeleteComment(c.commentNo)}
                                                                        className="bg-red-500 p-5 text-white font-bold"></button>
                                                                :
                                                                null
                                                        }
                                                        </>
                                                        :
                                                        <div className="inline border ms-10 p-10">
                                                             .
                                                        </div>
                                                }
                                            </>
                                    </div>
                                )
                            })}</div>
                        </div>
                    </div>
                )
            })}
            <input type="hidden" ref={typeRef} value="RELAY_BOARD"/>
        </article>
    );
};