import React, { useCallback, useEffect, useState } from 'react';
import axios from 'axios';
import '../../../css/boardList.css'
import {Link} from "react-router-dom";

export default function List () {
    // State   Hook
    const [boardList, setBoardList] = useState([]);                       //   
    const [searchWord, setSearchWord] = useState(null);                           // 
    const [searchType, setSearchType] = useState("podcastTitle");           //  
    const [memberId, setMemberId] = useState(null);                               //   

    //  
    const [totalBoardListCount, setTotalBoardListCount] = useState(0);  //   
    const [currentPage, setCurrentPage] = useState(1);                  //   

    const [endPage, setEndPage] = useState(0);                          //   
    const [pageNumList, setPageNumList] = useState([]);                  //   
    const pageNumListSize = 10;                                                       //   
    const boardType = "podcastBoard";                                                    // URL    

    // DB    
    const getData = async () => {
        const firstRecordIndex = (currentPage - 1) * pageNumListSize + 1; //  
        const res = await axios.get(`/${boardType}/list`,
            {
                params:{
                    firstRecordIndex:firstRecordIndex - 1,
                    searchWord:searchWord,
                    searchType:searchType,
                    pageNumListSize:pageNumListSize,
                }
            })
        if (res.data) {
            setBoardList(res.data.boardList);                  //   
            setTotalBoardListCount(res.data.boardListCount);   //   (    )
        }
    }

    //    handler
    const searchKeywordOnChangeHandler = useCallback((e) => {
        setSearchWord(e.target.value);
    }, []);

    const searchTypeOnChangeHandler = useCallback((e) => {
        setSearchType(e.target.value);
    }, []);

    //     
    const handleSearch = async  () => {
        const data = {
            searchType : searchType,
            searchWord : searchWord,
        }
        const res =
            await axios.get(`/${boardType}/search`, data)
        if (res.data) {
            setBoardList(res.data)
        }
    }

    //       
    const allList = () =>{
        setSearchWord("")
        getData();
    }

    // AMDIN   
    const goDelete = async (number)=> {
        if(window.confirm("  : " + number + "  ?")){
            const res = await axios.put(`/${boardType}/delete`, null,{
                params:{
                    podcastBoardNo : number,
                }
            })
            getData(); //    
        }
    }

    //   ( )
    const getPageNumList = (startNum) => {
        const list = [];
        for(let i = startNum; i < (startNum + pageNumListSize); i++){
            if(i <= endPage){
                list.push(i)
            }
        }
        setPageNumList(list);
        setCurrentPage(startNum);
    }

    //      
    useEffect(() => {
        getData();
    }, [currentPage]);

    //     
    useEffect(() => {
        const endNum = Math.ceil(totalBoardListCount / pageNumListSize);
        setEndPage(endNum)
    }, [totalBoardListCount]);

    //   
    useEffect(() => {
        getPageNumList(currentPage);
    }, [endPage]);

    useEffect(() => {
        getData()
        const memberLog = sessionStorage.getItem("member")
        if(memberLog){
            setMemberId(JSON.parse(memberLog)); //   memberId, memberNickname, memberAdmin  data 
        }
    }, []);

    //   
    const goClickPrev = () => {
        window.scrollTo({
            top:10,
            behavior:'smooth',
        });

        // TODO
        //    
        //  : 1 page         firstRecordIndex  -25    
        if(currentPage > 1) {
            getPageNumList(pageNumList[0] - pageNumListSize);
        }else{
            getPageNumList(1);
            return alert("1 .");
        }
    }

    //   
    const goClickNext = () => {
        window.scrollTo({
            top:10,
            behavior:'smooth',
        })

        // currentPage  endPage  , pageNumList[1,2,3,4,5] (5)  pageNumListSize(5) 
        //   0  2    'Next Button'  
        // pageNumList.length % pageNumListSize == 0    page   
        //       page  pageNumList  5  
        //   currentPage < endPage    
        if(currentPage < endPage && pageNumList.length % pageNumListSize == 0) {
            getPageNumList(pageNumList[0] + pageNumListSize)
        }else{
            return alert(" .");
        }
    }
    return (
        <article className="mt-32 ml-32 mr-32">
            <div className="text-black">
                <br/>
                <div className="text-center text-4xl mb-10"></div>
                <hr className="hr1" noshade/>
                <span> {totalBoardListCount}   .</span>
                <span className="right">
                    <select onChange={searchTypeOnChangeHandler}>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                    <input
                        onChange={searchKeywordOnChangeHandler}
                        type="text" value={searchWord}/>
                    <button
                        onClick={handleSearch}
                        name="" className="gradient">
                        
                    </button>
                </span>
                <br/>
                <table>
                    <thead>
                        <tr>
                            <th className="small-col"></th>
                            <th className="large-col"></th>
                            <th className="sl-col"></th>
                            <th className="middle-col"></th>
                            <th className="small-col"></th>
                            <th className="small-col"> </th>
                            {memberId && memberId.memberAdmin === 'ADMIN' ?
                                <th className="small-col"></th>
                                :
                                null
                            }
                        </tr>
                    </thead>
                    <tbody>
                        {boardList && boardList.map((board,i)=> (
                            <tr key={i}>
                                <td className="center small-col">
                                    <Link
                                        to={`/community/${boardType}/view`}
                                        state={board.podcastBoardNo}>
                                        {board.podcastBoardNo}
                                    </Link>
                                </td>
                                <td className="left large-col">
                                    <Link
                                        to={`/community/${boardType}/view`}
                                        state={board.podcastBoardNo}>
                                        {board.podcastTitle}&nbsp;&nbsp;
                                    </Link>
                                    {board.hits
                                        ?
                                        <span>
                                            <img src="/images/hits.png" className="w-5 inline"/>&nbsp;
                                            {board.hits}&nbsp;&nbsp;&nbsp;
                                            {board.onair === 'LIVE'
                                            ?
                                                <img src="/images/onair.png" className="w-10 inline"/>
                                            :
                                                null
                                            }
                                        </span>
                                        :
                                    null
                                    }
                                </td>
                                <td className="center small-col">{board.memberId}</td>
                                <td className="center sl-col">{new Date(board.createDate).toLocaleString('ko-kr', {month:"long", day:"numeric"})}</td>
                                <td className="center small-col">{board.hits}</td>
                                {board.onair === 'BEFORE'
                                    ?
                                    <td>  </td>
                                    :
                                    board.onair === 'LIVE'
                                        ?
                                        <td> </td>
                                        :
                                        <td> </td>
                                }
                                {memberId && memberId.memberAdmin === 'ADMIN' ?
                                    <td className="center small-col">
                                        <button
                                            onClick={()=> goDelete(board.podcastBoardNo)}
                                            className="text-red-700">v</button>
                                    </td>
                                :
                                    null
                                }
                            </tr>
                        ))}
                    </tbody>
                </table>
                <br/>
                <span className="right">
                    <Link to={`/community/${boardType}/list`}><button className="greylist" onClick={allList}></button></Link>
                    <Link to={`/community/${boardType}/add`}><button className="gradient"></button></Link>
                </span>
            </div>
                <div style={{display: "flex", justifyContent: "center", alignItems: "center"}}
                     className="flex items-center justify-between border-t border-gray-200 bg-white px-4 py-5 sm:px-6">

                    <nav className="isolate inline-flex -space-x-px rounded-md shadow-sm"
                         aria-label="Pagination">
                        <a
                            onClick={goClickPrev}
                            className="relative inline-flex items-center rounded-l-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                        >
                            <span className="sr-only">Previous</span>
                            <img
                                src="/images/chevron-left-solid.svg"
                                className="h-5 w-5" aria-hidden="true"
                            />
                        </a>
                        {
                            pageNumList.map(((v, i) => {
                                return (
                                    <Link
                                        className="relative inline-flex items-center px-4 py-2 text-sm font-semibold text-gray-900 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                                        key={`page` + i}
                                        to={`/community/${boardType}/list`}
                                        state={{currentPage:currentPage}}
                                        onClick={() => {
                                        //       
                                        setCurrentPage(v)
                                        //         
                                        window.scrollTo({
                                            top: 0,
                                            behavior: 'smooth',
                                        });
                                    }}>{v}</Link>
                                )
                            }))
                        }

                        <a
                            onClick={goClickNext}
                            className="relative inline-flex items-center rounded-r-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                        >
                            <span className="sr-only">Next</span>
                            <img
                                src="/images/chevron-right-solid.svg"
                                className="h-5 w-5" aria-hidden="true"
                            />
                        </a>
                    </nav>
                </div>
        </article>
    );
};