import {useEffect, useState} from "react";
import axios from "axios";
import {Link} from "react-router-dom";


export default function BookList() {

    /**
     *  : 
     *    
     * getDate() :        
     * */

    /*   */
    const [bookList, setBookList] = useState([]);
    /*   */
    const [currentPage, setCurrentPage] = useState(1);
    /*      */
    const recordsPerPage = 10;
    /*    */
    const [resultCount, setResultCount] = useState(0)

    /*  ,  (/) */
    const [searchWord, setSearchWord] = useState("")
    const [searchType, setSearchType] = useState("bookName")


    /*     getData  */
    const getData = async () => {
        try {
            const fristRecordIndex = (currentPage - 1) * 10 + 1;  //
            // const lastRecordIndex = currentPage * recordsPerPage //
            const res = await axios.get("/book/list", {
                params: {
                    firstRecordIndex: fristRecordIndex - 1,
                    // lastRecordIndex: lastRecordIndex,
                    searchWord: searchWord,
                    searchType: searchType
                }
            });
            setBookList(res.data.list);
            setResultCount(res.data.list.length);
        } catch (error) {
            console.error("Error fetching data:", error);
        }
    };

    /*     ,     */
    const searchWordOnChangeHandler = (e) => {
        setSearchWord(e.target.value)
    }
    const searchTypeOnChangeHandler = (e) => {
        setSearchType(e.target.value)
    }

    /*         useEffect  */
    useEffect(() => {
        getData();
    }, [currentPage]);

    /*     */
    const totalCount = 335; // 
    const [pageNumList, setPageNumList] = useState([]); // 
    const pageNumListSize = 10; //  (10)
    const totalPageNum = Math.ceil(totalCount / pageNumListSize); //   

    /*     */
    const getPageNumList = (startNum) => {
        const list = []
        for (let i = startNum; i < (startNum + pageNumListSize); i++) {
            if (i <= totalPageNum) {
                list.push(i)
            }
        }
        setPageNumList(list)
        setCurrentPage(startNum)
    }

    /*  pageNumList      1 default   */
    useEffect(() => {
        getPageNumList(1)
    }, []);


    /*     */
    const goClickPrev = () => {

        /*           */
        window.scrollTo({
            top: 0,
            behavior: 'smooth',
        });

        /* 1     */
        if (currentPage > 1) {
            getPageNumList(pageNumList[0] - 10);
        } else {
            // 1 ~ 10      
            getPageNumList(1);
            return alert(" .");
        }
    }


    /*     */
    const goClickNext = () => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth',
        });
        if (currentPage < totalPageNum && pageNumList.length % pageNumListSize == 0) {
            getPageNumList(pageNumList[0] + 10)

        }
        else {
            return alert(" .")
        }
    }
    /*    */
    const searchBook = (e) => {
        getData();

        /*          */
        e.preventDefault();
    }

    return (
        <main>
            <section className="gj do hj sp jr i pg rundry">
                <div className="bb ze ki xn 2xl:ud-px-0">
                    <div id="board-search">
                        <form action={`/book/list?searchWord=${searchWord}`} method="get">
                            <label>
                                <select name="searchType" onChange={searchTypeOnChangeHandler}>
                                    <option value="bookName"></option>
                                    <option value="bookAuthor"></option>
                                </select>
                            </label>
                            <label>
                                <input onChange={searchWordOnChangeHandler} type="text" name="searchWord"
                                       placeholder=" ."/>
                            </label>
                            <button type="text" onClick={searchBook} className="btn btn-dark"></button>
                        </form>
                    </div>

                    <div className="border-2 mt-5"/>

                    {bookList && bookList.length > 0 ? (
                        bookList.map((v, i) => (
                            <ul role="list" className="divide-y divide-gray-100" key={`bookList` + i}>
                                <Link to={`/book/view?bookNo=${v.bookNo}`}>
                                    <li className="flex justify-between gap-x-6 py-5">
                                        <div className="flex min-w-0 gap-x-4">
                                            <img className="h-60 w-60 flex-none rounded-md bg-gray-50 border-2 p-1"
                                                 src={v.bookImage}
                                                 alt={v.bookName}/>
                                            <div className="min-w-0 flex-auto mt-5 ml-5">
                                                <p className="text-xl font-bold leading-6 text-gray-900">
                                                    {v.bookName}
                                                </p>
                                                <p className="text-sm font-bold leading-6 text-black">
                                                    : {v.bookAuthor}</p>
                                                <p className="text-sm font-bold text-black"> : {v.bookCompany}</p>
                                                <p className="mt-1 text-sm leading-5 text-gray-500">
                                                    : {v.bookRelease}</p>
                                                <p className="mt-1 truncate text-sm leading-5 text-gray-900"> 
                                                    : {v.bookIntro}</p>
                                            </div>
                                        </div>
                                    </li>
                                </Link>
                            </ul>
                        ))
                    ) : (
                        null
                    )
                    }
                    {bookList.length < 10 && currentPage != totalPageNum ? null :
                        <div style={{display: "flex", justifyContent: "center", alignItems: "center"}}
                             className="flex items-center justify-between border-t border-gray-200 bg-white px-4 py-5 sm:px-6">

                            <nav className="isolate inline-flex -space-x-px rounded-md shadow-sm"
                                 aria-label="Pagination">
                                <a
                                    onClick={goClickPrev}
                                    className="relative inline-flex items-center rounded-l-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                                >
                                    <span className="sr-only">Previous</span>
                                    <img
                                        src="/images/chevron-left-solid.svg"
                                        className="h-5 w-5" aria-hidden="true"
                                    />
                                </a>
                                {
                                    pageNumList.map(((v, i) => {
                                        return (
                                            <Link
                                                className="relative inline-flex items-center px-4 py-2 text-sm font-semibold text-gray-900 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                                                key={`page` + i}
                                                to={`/book/list?currentPageNo=${v}`} onClick={() => {
                                                //       
                                                setCurrentPage(v)
                                                //         
                                                window.scrollTo({
                                                    top: 0,
                                                    behavior: 'smooth',
                                                });
                                            }}>{v}</Link>
                                        )
                                    }))
                                }

                                <a
                                    onClick={goClickNext}
                                    className="relative inline-flex items-center rounded-r-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                                >
                                    <span className="sr-only">Next</span>
                                    <img
                                        src="/images/chevron-right-solid.svg"
                                        className="h-5 w-5" aria-hidden="true"
                                    />
                                </a>
                            </nav>
                        </div>
                        //  
                    }

                </div>
            </section>
        </main>
    )
        ;
}


